*{
    margin:0%;
    padding:0%;
    font-family: 'Montserrat', sans-serif;
   
}
.heading{
    margin-left: 5px;
    margin-top: 10px;
    border-radius: 10px;
    padding: 10px 10px;
    color: white;
    background-color: teal;

}
/* .footer{
    margin-top:300px;
    margin-left: 400px;
    
} */


.tableh{
    color: rgb(193, 40, 231);
    
}
.app_header{
    display:flex;
    align-items: center;
    margin-bottom:33px ;
    justify-content: space-between;

    
}
.app_stats{
    display: flex;
    justify-content: space-between;
   
   
}
.app_left{
    flex:0.7;
}
.app{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
   background-image: url(./img.jpg);
   background-size: cover;
  
  

}
.app_right{
    margin-top: 80px;
}
.menu{
    padding:0px;
    border: none;
    border-radius: 20px;
    padding: 1px;
    background-color: rgb(74, 139, 243);
}
.globe{
    
    background-image: url(./globe.jpg);
    height: 300px;
    background-size: cover;
}
.slide{
    margin-top: 10px;
    color: thistle;
    background-color: teal;

}



@media (max-width: 990px) {
    .app{
        flex-direction: column;
    }
    
    .heading{
        margin-top:0px;
        padding-left:1px;
        padding-right: 1px;
        font-size: 13px;
    }
 
}

